export default {
	data() {
		return {
			timeout:10,
			showgetcode:0,
			timer:null,
			oldphone:'',
			newsphone:''
		}
	},
	methods: {
		getcode(){
			this.showgetcode = 1
			this.timer = setInterval(()=>{
				this.timeout--;
				if(this.timeout <= 0){
					clearInterval(this.timer)
					this.showgetcode = 2
					this.timeout = 10
				}
			},1000)
		}
	},
	destroyed() {
		if(this.timer){
					clearInterval(this.timer)
		}
	}
}
